import Bmob from "hydrogen-js-sdk"
export default {
	data() {
		return {
			isCard: true,
			blogList:[],
			objectId:'',
			isLazyLoad:true
		}
	},
	onLoad:function(option){
		this.objectId = option.objectId;
		this.initBlogList();
	},
	onShow() {
		this.initBlogList();
	},
	methods: {
		previewImg(list,index){
			uni.previewImage({
				urls:list,
				current:index,
				longPressActions:{
					itemList:['保存到相册'],
					success: function(data) {
					},
					fail: function(err) {
						console.log(err.errMsg);
					}
				}
			})
		},
		initBlogList(){
			var THAT = this;
			let query = Bmob.Query("MicroBlog");
			query.order("-updatedAt");
			query.equalTo("creator", '===', this.objectId);
			query.include('creator')
			query.find().then(res => {
				console.log(res);
				res.map((currentValue,index,arr)=>{
					if(res.imgList!=''){
						currentValue.imgList=currentValue.imgList.split(",");//分割imgList
					}
					let queryLike = Bmob.Query('MicroBlog')
					queryLike.field('like',currentValue.objectId)
					queryLike.relation('_User').then(result => {
					  console.log(index,result);
					  currentValue.likes = result;
					})
				})
				setTimeout(()=>{
					THAT.blogList=res;
				},0)	
			}).catch(err=>{
				console.log(err)
			})
		},
		deleteBlog(objectId){
			var THAT = this;
			uni.showModal({
				title:'提示',
				content:'确认删除?',
				success: (res) => {
					if(res.confirm==true){
						const query = Bmob.Query('MicroBlog');
						query.destroy(objectId).then(res => {
						  console.log(res)
						  THAT.initBlogList()
						}).catch(err => {
						  console.log(err)
						})
					}
				}
			})
		},
		toModifyBlog(objectId){
			uni.navigateTo({
				url:`ModifyBlog/ModifyBlog?objectId=${objectId}`,
				success:function(res){
					console.log(res);
				},
				fail:function(err){
					console.log(err);
				}
			})
		}
	}
}
